<template>
  <div ref="mapChart" class="mapChart-wrapper" style="height:calc(100% - 24px);width:100%">

  </div>
</template>

<script>
  import {chartMixin} from 'assets/js/common.js';

  export default {
    name: "ageBar",
    mixins: [chartMixin],
    props: {
      echartsData: {
        type: Object,
        default: () => {
        }
      }
    },
    data() {
      return {}
    },
    watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.myChart = this.$echarts.init(this.$refs.mapChart);

      this.$nextTick(() => {
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let option = {
          // color: ['#149be2'],
          tooltip: {
            trigger: 'axis',
            backgroundColor: 'rgba(24, 47, 73, 0.9)',
            borderColor: '#167cc4',
            borderWidth: 1,
            extraCssText: 'box-shadow: 0 0 3px rgba(255, 255, 255, 0.3);',
            textStyle: {
              color: '#fff'
            },
            axisPointer: {
              type: 'shadow',
              shadowStyle: {
                color: "rgba(255,255,255, 0.1)"
              }
            },
            formatter: function (params) {
              let str = ''
              params.forEach((val, index) => {
                str += `${val.name}:<span style="color: #e9d621">${val.value}</span>`
              })
              return str
            }
          },
          grid: {
            show: true,
            left: '30',
            right: '20',
            bottom: '20',
            containLabel: true,
            backgroundColor: {
              type: 'linear',
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [{
                offset: 0, color: 'rgba(40,82,181, 0)' // 0% 处的颜色
              }, {
                offset: 1, color: 'rgba(40,82,181, 0.5)' // 100% 处的颜色
              }],
              globalCoord: true // 缺省为 false
            },
            borderColor: 'transparent'
          },
          xAxis: [
            {
              type: 'category',
              data: this.echartsData.xdata,
              axisLine: {
                lineStyle: {
                  color: '#1e9ce4'
                }
              },
              axisLabel: {
                margin: 20,
                color: '#fff',
                fontSize: 14
              }
            }
          ],
          yAxis: [
            {
              type: 'value',
              name: '人数',
              nameGap: '15',
              nameTextStyle: {
                color: '#fff',
                fontSize: 14
              },
              splitLine: {
                lineStyle: {
                  color: '#165ba1',
                  type: 'dashed',
                  opacity: 0.7
                }
              },
              axisLine: {
                show: false
              },
              axisTick: {
                show: false
              },
              axisLabel: {
                color: '#fff',
                fontSize: 14
              }
            }
          ],
          series: [
            {
              name: '年龄',
              type: 'bar',
              barWidth: '22',
              itemStyle: {
                normal: {
                  color: new this.$echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#e69143'},
                      {offset: 1, color: '#e3690d'}
                    ]
                  ),
                  opacity: 1,
                  borderWidth: 1,
                  borderColor: '#f99200'
                },
                emphasis: {
                  color: new this.$echarts.graphic.LinearGradient(
                    0, 0, 0, 1,
                    [
                      {offset: 0, color: '#e9d621'},
                      {offset: 1, color: '#e6ad0e'}
                    ]
                  ),
                  opacity: 1,
                  borderColor: '#ffe400'
                }
              },
              data: this.echartsData.data
            }
          ]
        }
        this.myChart.setOption(option);
      },
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
    },
  }
</script>

<style lang="scss" scoped>

</style>



