<template>
  <div class="footer-wrapper" :class="{'add-bgc': isLoginFooter}">
    <div class="footer-info">
      <div class="company">
        <span>版权所有：</span>
        <span>九次方大数据信息集团有限公司</span>
      </div>
      <div class="contact">
        <span>咨询电话：</span>
        <span>400-819-0919</span>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: "footers",
    props: {
      isLoginFooter: {
        type: Boolean,
        default: false
      }
    }
  }
</script>

<style lang="scss" scoped>
  @import "../assets/css/variable";
  @import "../assets/css/mixin";

  .footer-wrapper {
    height: 64px;
    margin-top: -64px;
    width: 100%;
    min-width: 1200px;
    background: #243446;
    position: relative;
    .footer-info {
      color: #a8c4f6;
      font-size: $fz-sm;
      font-weight: normal;
      width: $site-width;
      height: 64px;
      margin: 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;
      .company {
        padding-right: 30px;
        & span:nth-child(1) {
          color: #49637c;
        }
      }
      .contact {
        padding-left: 30px;
        & span:nth-child(1) {
          color: #49637c;
        }
      }
    }
  }
  .add-bgc {
    background: linear-gradient(to right, #0a0f24, #081f5a, #0a0f24) !important;
  }
</style>



