<template>
  <div class="login-wrap">
    <div class="login-box">
      <div class="login_title"></div>
      <div class="login_content">
        <div class="conter-box">
          <div class="login-user">
            <i class="icon iconfont icon-yonghu"></i>
            <input ref="usr" v-model="username" autofocus="autofocus" placeholder="请输入用户名" @keyup.enter.stop="nextInput"/>
          </div>
          <div class="login-password">
            <i class="icon iconfont icon-icon2"></i>
            <input ref="pwd" v-model="password" :class="{'err':isErr}" placeholder="请输入密码" type="password" @keyup.enter.stop="login"/>
            <i class="icon iconfont  i-ciphertext" :class="{'icon-signeyeoff': !flag,'icon-signeyeon': flag}" @click.stop="togglePwd"></i>
          </div>
          <button class="login-register" :class="{'disable': !allowLongin}" @click.stop="login" :disabled="!allowLongin">{{allowLongin?'登录':'登录中...'}}</button>
          <p class="login-warn" v-show="showWarning"><i class="icon iconfont icon-yunongtongjingshigantanhao"></i>您输入的用户名或密码错误！</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "login",
    data() {
      return {
        username: '',
        password: '',
        // 显示或者隐藏密码
        flag: true,
        // 密码错误时候
        isErr: false,
        showWarning: false,
        allowLongin: true
      }
    },
    props: {},
    mounted() {
      setTimeout(() => {
        window.addEventListener('keyup', this.listenEnter)
      })
    },
    methods: {
      /***
       * 监听键盘事件
       */
      listenEnter(ev) {
        if (ev.keyCode === 13) {
          this.login()
        }
      },
      nextInput() {
        this.$refs.pwd.focus()
        this.$refs.pwd.select()
      },
      /***
       * 登录
       */
      login() {
        console.log('ggggo')
        if(this.username && this.password){
          this.doTest()
        } else {
          this.$refs.usr.focus()
          this.$refs.usr.select()
          this.showWarning = true
        }
      },
      /***
       * 显示和隐藏密码
       */
      togglePwd() {
        if(this.flag){
          this.$refs.pwd.type = 'text'
        } else {
          this.$refs.pwd.type = 'password'
        }
        this.flag = !this.flag
      },
      /***
       * 发送请求验证登录信息
       */
      verify() {
        // go verify
      },
      doTest() {
        this.allowLongin = false
        setTimeout(()=>{
          localStorage.setItem('accessToken', this.username);
          this.$router.push('/index')
        },1500)
      }
    },
    beforeRouteEnter(to, from, next) {
      next((vm) => {
          // console.log(vm.$router)
          // vm.$router.app.$emit('change')
        }
      )
    },
    beforeDestroy() {
      window.removeEventListener('keyup', this.listenEnter)
    }
  }
</script>

<style scoped lang="scss">
  @import "../../assets/css/login.css";

  .login-wrap {
    width: 100%;
    min-width: 1200px;
    height: 100%;
    min-height: 600px;
    position: relative;
    .login-box {
      width: 625px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      .disable {
        cursor: not-allowed;
      }
    }
    .login-password {
      .err {
        color: #f0000f;
      }
    }
    .login-register:focus {
      text-shadow:none;
      outline: none;
      background: rgba(225,225,225,1);
      box-shadow: 0 0 2px 3px #d35400;
    }
  }
</style>



