<template>
  <div class="contain">
    <div class="nav">
      <div class="links">
        <ul class="link-list">
          <router-link tag="li" v-for="item,index in items" :to="item.to" :key="index"><span>{{item.name}}</span></router-link>
        </ul>
        <jSelect style="width: 625px;margin-left: 100px;padding-top:1px"></jSelect>
      </div>
    </div>
     <div class="wrap-main">
         <div class="wrap-left wrap-part wrap-border">
           <div id="wrap-map" style="width:100%;height:100%;"></div>
         </div>
         <div class="wrap-right wrap-part">
          <div class="up-charts">
            <div class="wrap-place wrap-border">
              <p class="wrap-title">犯罪行为地域分布-地区属性</p>
              <pieChart></pieChart>
            </div>
            <div class="wrap-abroad wrap-border">
              <p class="wrap-title">犯罪行为地域分布-室内室外</p>
              <lineChart></lineChart>
            </div>
          </div>
           <div class="wrap-police wrap-border">
             <p class="wrap-title">犯罪行为地域分布-派出所辖区</p>
           </div>
         </div>
     </div>
  </div>
</template>
<script type="text/javascript">
  import axios from 'axios';
  import {chartMixin} from 'assets/js/common';
  import jSelect from '../../components/jSelect.vue';
  import lineChart from './charts/line.vue'
  import pieChart from './charts/pie.vue'
  export default {
    // mixins: [chartMixin],
    data() {
      return {
        abortIndex: 1,//忽略默认项序号
        items: [
          {
            to: '/analyse/timedis',
            name: '时间分布'
          },
          {
            to: '/analyse/areas',
            name:'地域分布'
          },
          {
            to: '/analyse/type',
            name: '类型分布'
          },
          {
            to: '/analyse/person',
            name: '人员分布'}
        ]
      }
    },
    methods:{
      drawMap(ele,option){
        let _this = this;
        axios.get("static/hubei.json").then(function(res){
          let geoJson = res.data;
          let dataJson=[];
          //   根据传入的option生成的 option.series.data
          for(let item of geoJson.features){
            let base={name:item.properties.name,value:Math.floor(Math.random()*100)};
            dataJson.push(base);
          }
          option.series.data=dataJson;
          _this.$echarts.registerMap('huBei', geoJson);
          var myEcharts = _this.$echarts.init(document.getElementById(ele));
          myEcharts.setOption(option);
        })
      },
    },
    components: {
      jSelect,
      lineChart,
      pieChart
    }
    ,
    mounted(){
      let mapOption = {
        visualMap: {
          show:true,
          min: 0,
          max: 100,
          right: '50',
          bottom: '10',
          text: ['高 100', '低 0'], // 文本，默认为数值文本
          itemWidth: 10,
          itemHeight: 100,
          color: ["rgba(1,130,204,0.8)", 'rgba(20,76,168,0.8)'],
          textStyle: {
            color: '#fff'
          },
        },
        tooltip: {
          trigger: 'item',
          padding:[5,10],
          borderColor: '#8398bd',
          borderWidth: 2,
          formatter:' <span style="color: #fddf16;font-size: 22px;">{c}</span>起'
        },
        geo: {
          map: 'huBei',
          label: {
            normal: {
              show: true,
              color:'#fff'
            },
            emphasis:{
              color:'#fff'
            }
          },
          roam: true,
          top:15,
          bottom:15,
          itemStyle: {
            normal: {
              borderWidth:5,
              borderColor:'#5db7e8',
            },
            emphasis: {
              areaColor: '#e58a39',
            }

          }
        },
        series:{
          type: 'map',
          mapType: 'huBei',
          geoIndex: 0,
          label: {
            normal: {
              show: true
            },
            emphasis: {
              show: true
            }
          },
          data: [
            { name: '北京', value: 3 },
            { name: '天津', value: 4 },
            { name: '上海', value: 1 },
            { name: '重庆', value: 3 },
            { name: '河北', value: 2 },
            { name: '河南', value: 1 },
            { name: '云南', value: 2 },
            { name: '辽宁', value: 100 },
            { name: '黑龙江', value: 1 },
            { name: '湖南', value: 100},
            { name: '安徽', value: 1 },
            { name: '山东', value:200 },
            { name: '新疆', value: 200},
            { name: '江苏', value: 1 },
            { name: '浙江', value: 200 },
            { name: '江西', value: 200 },
            { name: '湖北', value: 100 },
            { name: '广西', value: 140 },
            { name: '甘肃', value: 2 },
            { name: '山西', value: 200 },
            { name: '内蒙古', value: 50 },
            { name: '陕西', value: 2 },
            { name: '吉林', value: 2 },
            { name: '福建', value: 70 },
            { name: '贵州', value: 1 },
            { name: '广东', value: 2 },
            { name: '青海', value: 100 },
            { name: '西藏', value: 100 },
            { name: '四川', value: 200 },
            { name: '宁夏', value: 1 },
            { name: '海南', value: 2 },
            { name: '台湾', value: 1 },
            { name: '香港', value: 1 },
            { name: '澳门', value: 2 }
          ]
        }
      };
      this.drawMap('wrap-map',mapOption);
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/_mixin.scss";
  @import "../../assets/css/_variable.scss";
  /*缺角宽度*/
  $width-angle: 8px;
  /*边框长度  减去的宽度 为 width-angle 的 2 倍*/
  $width: calc(100% - 16px);

  /*边框颜色*/
  $color-border: #3780b9;

  /*box 盒子的背景色*/
  $color-background: #053767;

  /*调薪渐变颜色*/
  $color-linear: rgba(255, 255, 255, 0.008);
  .contain{
    min-width:1200px;
    height:calc(100vh - 204px);
  }
  .nav {
    padding-left: 10px;
    .links {
      .link-list {
      	vertical-align: top;
        display: inline-flex;
        justify-content: flex-start;
        align-items: center;
        li {
          color: #b2d7ff;
          font-size: 16px;
          cursor: pointer;
          width: 98px;
          height: 30px;
          padding: 0 10px;
          text-align: center;
          box-sizing: border-box;
          background-color: #143759;
          border: 1px #54b0ff solid;
          border-right: none;
          transform:skewX(-30deg);
          @include animation();
          &:nth-last-child(1) {
            border-right: 1px #54b0ff solid;
          }
          span {
            height: 30px;
            line-height: 30px;
            display: inline-block;
            transform:skewX(30deg);
          }
          &:hover, &.active {
            color: #fff;
            background-color: #2c72ba;
          }
        }
      }
    }
  }
  .wrap-main{
    width: 100%;
    height:calc(100% - 70px);
    padding-top: 40px;
    .wrap-part{
      width:50%;
      height:100%;
      float: left;
      box-sizing:border-box;
    }
    .wrap-left{
      border-radius:10px;
      background: rgba(0,74,142,0.4) url(../../assets/img/map_bg.png) no-repeat;
      background-size: cover;
      overflow: hidden;
    }
    .wrap-border{
      border-radius:5px;
      border:1px solid #357fb7;
    }
    .wrap-right{
      padding-left:20px;
      .up-charts {
        height: calc(50% - 20px);
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;
        .wrap-place,.wrap-abroad{
          box-sizing:border-box;
          width:49%;
          position: relative;
          background:-webkit-linear-gradient(to bottom, rgba(0,74,142,0.7), rgba(5,42,84,0.7));
          background:linear-gradient(to bottom, rgba(0,74,142,0.7), rgba(5,42,84,0.7));
          height:100%;
        }
      }
    }

    .wrap-abroad{
      margin-left:2%;
    }
    .wrap-police{
      clear: both;
      position: relative;
      width:100%;
      height: calc(50% - 0px);
      box-sizing: border-box;
      background:rgba(0,74,142,0.5) url(../../assets/img/gif.jpg) no-repeat 0 70px;
      background-size: cover;
    }
    .wrap-title{
      height:44px;
      line-height: 44px;
      font-size: 16px;
      position: relative;
      left:-5px;
      background:url(../../assets/img/title.png) no-repeat center left;
      text-indent: 30px;
    }
  }
</style>



