<template>
  <div class="jSelect" :style="minWidth">
    <div class="select-item" v-for="option,index in options">
      <span class="label">{{labels[index]}}&nbsp;:&nbsp;</span>
      <el-select v-model="value[index]" :placeholder="placeholder" @change="change">
        <el-option
          v-for="item in option"
          :key="item.value"
          :label="item.value"
          :value="item.value">
        </el-option>
      </el-select>
    </div>
  </div>
</template>
<script>
  export default{
    name: 'jSelect',
    props: {
      placeholder: {
        type: String,
        default: '请选择'
      },
      initSelects: {
        type: Array,
        default: function () {
          return [[
            {
              "value": "杀人"
            },
            {
              "value": "抢劫"
            },
            {
              "value": "盗窃"
            },
            {
              "value": "强奸"
            },
            {
              "value": "故意伤害"
            },
            {
              "value": "贩卖毒品"
            },
            {
              "value": "经济犯罪"
            },
            {
              "value": "其他"
            },
            {
              "value": "所有"
            }
          ], [
            {
              "value": "一个月内"
            },
            {
              "value": "一周内"
            },
            {
              "value": "三个月内"
            },
            {
              "value": "半年内"
            },
            {
              "value": "一年内"
            },
            {
              "value": "自定义"
            }
          ], [
            {
              "value": "所属辖区"
            },
            {
              "value": "网格选取"
            },
            {
              "value": "地图圈选"
            },
            {
              "value": "地图框选"
            }
          ]]
        }
      },
      initLabels: {
        type: Array,
        default: function () {
          return ['犯罪类型', '时间范围', '地域范围']
        }
      },
      initIndex: {
        type: Number,
        default: -1
      },
      initValue: {
        type: Array,
        default: function () {
          return ['盗窃', '一年内', '所属辖区']
        }
      },
      abortIndex: {//设置,默认3个中,不显示的序号
        type: Number,
        default: -1
      }
    },
    data(){
      return {
        rawIndex: this.initIndex,
        value: []
      }
    },
    computed: {
      options () {
        return this.getArr(this.initSelects)
      },
      labels () {
        return this.getArr(this.initLabels)
      },
      minWidth(){
        let idx = this.abortIndex !== undefined ? this.abortIndex : -1
        let length = this.initSelects.length
        if (idx !== -1 && idx < length && idx > 0) {
          length--
        }
        return 'min-width:' + ( length * 184 + 30) + 'px'
      }
    },
    methods: {
      getArr(value){
        let arr = []
        let idx = this.abortIndex !== undefined ? this.abortIndex : -1
        value.map(function (item, index) {
          if (index !== idx) {
            arr.push(item)
          }
        })
        return arr
      },
      change(){
        this.$emit('input', this.value)
        this.$emit('change', this.value)
      }
    },
    created(){
      let abortIndex = this.abortIndex !== undefined ? this.abortIndex : -1
      let initIndex = this.initIndex !== undefined ? this.initIndex : -1
      if (abortIndex !== -1) {
        if (initIndex !== -1) {
          let arr = []
          this.initSelects.map(function (item, index) {
            if (index !== abortIndex) {
              if (item[initIndex]) {
                arr.push(item[initIndex].value)
              } else {
                arr.push(item[0].value)
              }
            }
          })
          this.value = arr
        } else {
          this.value = this.getArr(this.initValue)
        }
      } else {
        this.value = this.getArr(this.initValue)
      }

      this.$emit('input', this.value)
//      this.$nextTick(()=> {
//        this.$emit('change', this.value)
//      })
    }
  }
</script>
<style lang="scss" scoped>
  .jSelect {
    width: 100%;
    display: inline-flex;
    justify-content: space-between;
    align-items: center;

  .select-item {
    display: inline-block;
  }

  .label {
    color: #ffffff;
  }

  .el-select /deep/ .el-input__inner {
    width: 110px;
    height: 28px;
    /*background-image: linear-gradient(rgba(63, 167, 255, 0.17), rgba(63, 167, 255, 0.17)), linear-gradient(#0a246a, #0a246a) !important;*/
    /*background-blend-mode: normal, normal !important;*/
    background-color: rgba(5, 42, 84, 0.8);
    border-radius: 3px !important;
    border: solid 1px #47aaff !important;
    color: #ffffff;
  }

  .el-select /deep/ .el-select__caret {
    color: #94bfdc;
  }

  }

  .el-select-dropdown .el-popper /deep/ .el-select-dropdown.el-popper {
    background-color: rgba(5, 42, 84, 0.8);
    border: solid 1px #47aaff !important;
  }

</style>



