<template>
  <div class="chart-wrapper" ref="chart" style="height:calc(100% - 24px);width:100%">

  </div>
</template>

<script>
  import {chartMixin} from 'assets/js/common'
  export default {
    name: 'bar',
    mixins: [chartMixin],
    props: {
      echartsData: {
        type: Object,
        default: {}
      }
    },
    data() {
      return {}
    },
    watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart();
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let option = {
          backgroundColor: 'rgba(5,55,105,1)',
          tooltip : {
            trigger: 'axis',
            backgroundColor:'transparent',
            formatter: function(param){
              var html='';
              return html +='<div style="text-align:center;position:relative;padding: 5px 10px;line-height:22px;background:rgba(27,46,70,0.85);border:1px solid #167cc4;border-radius:5px;color:#7a7a7a;box-shadow:0 0 10px 0 rgba(40,157,252,.6);">'+
                      '<span style="font-size:14px;color:#fddf16;font-weight:bold;"><span style="color:#fff;">'+param[0].name+'：</span>'+param[0].value+'</span>'+
                      '<i style="position:absolute;bottom:-12px;left:50%;margin-left:-3px;width:0;height:0;border:6px solid transparent;border-top:6px solid #167cc4;"></i>'+
                      '<i style="position:absolute;bottom:-9px;left:50%;width:0;height:0;margin-left:-2px;border:5px solid transparent;border-top:5px solid rgba(27,46,70,0.85);"></i>'+
                      '</div>'
            }

          },
          legend: {
            left: 'right',
            data: [{
              name: "",
              textStyle: {
                color:"#fff"
              }
            },
              {
                name: "",
                textStyle: {
                  color:"#fff"
                }
              }]
          },
          label: {
            normal: {
              textStyle: {
                color: "#ff3b00"
              }
            },
            emphasis:{
              textStyle:{
                color:"#ff3b00"
              }
            }
          },
          grid: {
            left: '1%',
            right: '1%',
            bottom: '1%',
            top:'80',
            containLabel: true,
            show:true,
            backgroundColor:new this.$echarts.graphic.LinearGradient(0,0, 0, 1, [{
              offset: 0,
              color: 'rgba(5,55,103,1)'
            }, {
              offset: 1,
              color: 'rgba(24,62,128,1)'
            }]),
            borderColor:'#3780b9',
            borderWidth:0
          },
          yAxis: [{
            name: "",
            type: 'value',
            min:'0',
            axisTick: {
              show: false
            },
            splitLine:{
              show: true,
              lineStyle:{
                color:'#0f5a87',
                type:'dashed',
                width:1
              }
            },
            axisLine: {
              lineStyle: {
                color:'#fff',
                width: 0
              }
            },
            axisLabel:{
              textStyle:{
                color:"#fff",
                fontSize:15
              }
            }
          }],

          xAxis: {
            type: 'category',
            name: '',
            splitLine: {
              show:false,
              lineStyle: {
                color: ['#254495'],
                type:'dashed'
              }
            },
            axisTick:{
              show:false
            },
            axisLabel: {
              show: true
            },
            axisLine:{
              lineStyle: {
                color:'#096bad',
                fontSize: 16,
                width: 1
              }
            },
            axisLabel:{
              textStyle:{
                color:"#fff",
                fontSize:16
              }
            },
            axisPointer: {
              type: 'shadow',
              shadowStyle:{
                color:'rgba(255,255,255,0.1)'
              }
            },
            data : this.echartsData.xdata,
          },
          series: [
            {
              name: '',
              type: 'bar',
              barWidth : 20,
              label: {
                normal: {
                  show: false,
                  position: 'right',
                  formatter: "{c}%"
                }
              },
              itemStyle: {
                normal: {
                  barBorderRadius: [0],
                  color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: this.echartsData.barcolor[0]
                  }, {
                    offset: 1,
                    color: this.echartsData.barcolor[1]
                  }])
                },

                emphasis: {
                  color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                    offset: 0,
                    color: 'rgba(255,231,25,0.9)'
                  }, {
                    offset: 1,
                    color: 'rgba(250,182,0,0.9)'
                  }])
                }
              },
              data:this.echartsData.seriesdata
            }
          ]
        };
        this.myChart.setOption(option);

      },
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
    },
  }
</script>
<style lang="scss" scoped>

</style>



