<template>
  <div class="chart-wrapper" id="wrap-pie" ref="chart" style="height:calc(100% - 60px);width:100%">
  </div>
</template>

<script>
  import {chartMixin} from 'assets/js/common'
  export default {
    mixins: [chartMixin],
    data() {
      return {}
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart(){
        let option = {
          series: [
            {
              z: 0,
              name: '',
              type: 'pie',
              center: ['50%', '50%'],
              radius: ['59%', '60%'],
              label:{
                normal:{
                  position:'center',
                  show: false
                }
              },
              labelLine: {
                normal: {
                  show: false

                }
              },
              data: [{
                value: 0,
                itemStyle: {
                  normal: {
                    color: '#114cae' //颜色填充
                  }
                },
                tooltip:{show:false}
              }]
            },
            {
              z: 0,
              name: '',
              type: 'pie',
              center: ['50%', '50%'],
              radius: ['26%', '27%'],
              label:{
                normal:{
                  position:'center',
                  show: false
                }
              },
              labelLine: {
                normal: {
                  show: false

                }
              },
              data: [{
                value: 0,
                itemStyle: {
                  normal: {
                    color: '#114cae' //颜色填充
                  }
                },
                tooltip:{show:false}
              }]
            },
            {
              name:'',
              type:'pie',
              radius: ['30%', '50%'],
              center: ['50%', '50%'],
              data:[{
                value: 20,
                name: '住宅',
                itemStyle: {
                  normal: {
                    borderWidth:2,
                    color: 'rgba(22,150,210,.7)',
                    borderColor: '#09abf7',
                    shadowColor: 'rgba(0,230,252, 0.6)'
                  },
                  emphasis: {
                    color: '#f48822',
                    shadowBlur: 20,
                    borderColor: '#f9a40e',
                    shadowOffsetX: 0,
                  }
                }
              },{
                value: 40,
                name: '学校',
                itemStyle: {
                  normal: {
                    borderWidth:2,
                    color: 'rgba(22,150,210,.7)',
                    borderColor: '#09abf7',
                    shadowColor: 'rgba(0,230,252, 0.6)'
                  },
                  emphasis: {
                    color: '#f48822',
                    shadowBlur: 20,
                    borderColor: '#f9a40e',
                    shadowOffsetX: 0,
                  }
                }
              },{
                value: 40,
                name: '其他',
                itemStyle: {
                  normal: {
                    borderWidth:2,
                    color: 'rgba(22,150,210,.7)',
                    borderColor: '#09abf7',
                    shadowColor: 'rgba(0,230,252, 0.6)'
                  },
                  emphasis: {
                    color: '#f48822',
                    shadowBlur: 20,
                    borderColor: '#f9a40e',
                    shadowOffsetX: 0,
                  }
                }
              },{
                value: 20,
                name: '烂尾楼',
                itemStyle: {
                  normal: {
                    borderWidth:2,
                    color: 'rgba(22,150,210,.7)',
                    borderColor: '#09abf7',
                    shadowColor: 'rgba(0,230,252, 0.6)'
                  },
                  emphasis: {
                    color: '#f48822',
                    shadowBlur: 20,
                    borderColor: '#f9a40e',
                    shadowOffsetX: 0,
                  }
                }
              },{
                value: 40,
                name: '商场',
                itemStyle: {
                  normal: {
                    borderWidth:2,
                    color: 'rgba(22,150,210,.7)',
                    borderColor: '#09abf7',
                    shadowColor: 'rgba(0,230,252, 0.6)'
                  },
                  emphasis: {
                    color: '#f48822',
                    shadowBlur: 20,
                    borderColor: '#f9a40e',
                    shadowOffsetX: 0,
                  }
                }
              }],
              itemStyle: {
                emphasis: {
                  color: '#f48822',
                  shadowBlur: 20,
                  shadowOffsetX: 0,
                }
              },
              label: {
                normal: {
                  position: 'outside',
                  show: true,
                  formatter: "{b}",
                  textStyle: {
                    fontSize: 16,
                    color:'#fff'
                  }
                },
                emphasis: {
                  position: 'outside',
                  formatter: "{d}% 29\n{b}",
                  show: true,
                  textStyle: {
                    fontWeight: 'bold',
                    color:'#f5b522'
                  }
                },
              },

              labelLine: {
                normal: {
                  length:18,
                  length2:30,
                  lineStyle: {
                    opacity:0
                  }
                },
                emphasis: {
                  length:50,
                  length2:80,
                  lineStyle: {
                    color: "#fff",
                    opacity:1
                  }
                }
              }
            }
          ]
        };
        this.myChart.setOption(option);
      },
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
    },
  }
</script>
<style lang="scss" scoped>

</style>



