<template>
	<div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

	</div>
</template>

<script>
	import {chartMixin} from 'assets/js/common'
	export default {
		name: 'bar',
		mixins: [chartMixin],
		props: {
			criminalActHourData: {
				type: Object,
				default: () => {}
			}
		},
		data() {
			return {}
		},
		watch: {
			criminalActHourData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
//				console.log(this.criminalActHourData)
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			initChart() {
				let option = {
					color: ['#3398DB'],
					tooltip: {
						padding:[10,20],
						trigger: 'axis',
						backgroundColor:"rgba(27,46,70,0.85)",
						borderColor:"#167cc4",
						borderWidth:"1",
						axisPointer: {
							show: true,
							type: 'line',
							snap: true,
							lineStyle: {
								color: '#ffa125',
								width: 1,
								type: "dashed"
							}
						},
						textStyle:{
							color:"#fff",
//							fontWeight:'bold'
						},
						formatter:(params)=>{
							return 	`<div style="color:#fff;font-size:12px"><span style="color:#FDDF16;font-size:18px">${params[0].data}</span>起<br/>${params[0].axisValue}</div>`
						},
						extraCssText: 'box-shadow: 0 0 3px 1px rgba(255, 255, 255, 0.3);'
					},
					grid: {
						left: '5%',
						right: '6%',
						bottom: '3%',
						top: "14%",
						containLabel: true
					},
					xAxis: {
						nameGap:29,
						type: 'category',
						data: this.criminalActHourData.time,
			            boundaryGap: false,   //坐标轴的留白
			            axisLine: {      //隐藏X轴线
			              show: true,
			              lineStyle: {
			                color: "#0592EA",
			              }
			            },
			            axisTick:{
							show:false //坐标轴刻度
						},
			            axisLabel: {
                            margin:17,
                            rotate:40,
							textStyle: {
								color: '#fff',
								fontSize: 14,
							},
						},
					},
					yAxis: {
						type: 'value',
						name: '',
						axisTick:{
							show:false //坐标轴刻度
						},
						axisLine: {
							show: false,
						},
						axisLabel: {
							textStyle: {
								color: '#fff',
								fontSize: 14,
							},
						},
						splitLine: {
							lineStyle: {
								color: '#166DD4',
								type: "dashed",

							}
						},
					},
//					legend: {
//						data: ["可疑人员"],
//						color: "#099D4F",
//						itemWidth: 12,
//						itemHeight: 3,
//						itemGap: 28,
//						icon: "line",
//						textStyle: {
//							color: "#fff",
//							fontSize: 12,
//						},
//						top:0,
//					},
					series: {
							name: '可疑人员',
							type: 'line',
							showSymbol:false,
							barWidth: '60%',
							smooth: true,
							itemStyle: {
								normal: {
									color: "#f28707"
								},
								emphasis: {
									opacity: 1
								}
							},
							areaStyle:{
								normal:{
									color: {
									    type: 'linear',
									    x: 0,
									    y: 0,
									    x2: 0,
									    y2: 1,
									    colorStops: [{
									        offset: 0, color: '#ff7022' // 0% 处的颜色
									    }, {
									        offset: 1, color: 'rgba(255,112,34,0)' // 100% 处的颜色
									    }],
									}
								}
							},
							data: this.criminalActHourData.suspiciousperson
						},
				}
				this.myChart.on('click', (params) => {
					this.$emit('handleCharts', params.name)
				})
				this.myChart.setOption(option);

			},
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



