<template>
  <div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

  </div>
</template>

<script>
  import {chartMixin} from 'assets/js/common'

  export default {
    name: 'reasonDistribute',
    mixins: [chartMixin],
    props: {
      criminalActQuarterData: {
        type: Array,
        default: () => []
      }
    },
    data() {
      return {}
    },
    watch: {
      criminalActQuarterData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let option = {
          color: ['#3398DB'],
          tooltip: {
            show: false,
            formatter:  (params)=> {
              var html = '';
              return html = `<div><p style="font-size:26px;font-weight:bold;margin-bottom:0px;text-align:center;width:74px;color:#fff;">${params.percent}<span style="color:#fff;font-size:14px;">%</span></p>
                            <p style="text-align: center;font-size: 12px">${params.name}</p></div>`
            },
            // position: ['38%', '35%'],
            backgroundColor: 'none',
            position: function (point, params, dom, rect, size) {
              return [size.viewSize[0]/2-42, '40%']
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },

          series: [
            {
              // 外圈
              z: 0,
              name: '',
              type: 'pie',
              hoverAnimation: false,
              center: ['50%', '50%'],
              radius: ['74%', '75%'],
              label: {
                normal: {
                  position: 'center',
                  show: false
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [{
                value: 0,
                itemStyle: {
                  normal: {
                    color: '#1448ad' //颜色填充
                  },
                  emphasis: {
                    color: '#e7be14',
                  }
                },
                tooltip: {show: false}
              }]
            },
            {
              // 内圈
              z: 0,
              name: '',
              type: 'pie',
              hoverAnimation: false,
              center: ['50%', '50%'],
              radius: ['37%', '38%'],
              label: {
                normal: {
                  position: 'center',
                  show: false
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [{
                value: 0,
                itemStyle: {
                  normal: {
                    color: '#2c6cf1' //颜色填充
                  },
                  emphasis: {
                    color: '#e7be14',
                  }
                },
                tooltip: {show: false}
              }]
            },
            {
              name: '',
              type: 'pie',
              radius: ['40%', '63%'],
              center: ['50%', '50%'],
              data: this.criminalActQuarterData,
              itemStyle: {
                normal: {
                  borderColor: 'rgba(9, 31, 85, 1)',
                  borderWidth: 1,

                },
                emphasis: {
                  color: '#F3851D',
                }
              },
              label: {
                normal: {
                  color: '#fff',
//                position: ["70%","50%"],
                  position: "outside",
                  fontSize:14,
                  margin:20,
                  show: true,
                },
                emphasis:{
                	color:"#FFBC1C",
                	fontSize:18,
                	formatter:  "{c}%  {d}\n{b}",
                },
              },
              labelLine: {
                normal: {
                 show:false,
                 length:15,
                },
                emphasis:{
                	show:true,
                	lineStyle:{
                		color:"#fff",
                	}
                }
              }
            }
          ]
        }

        this.myChart.setOption(option);
      },
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



