<template>
  <div class="right-layout">
    <partLayout :title="title1" @setClick="setClick">
      <div slot="layoutBody">
        <el-radio-group v-model="radio1" @change="change">
          <el-radio v-for="item in radioList1" :label="item" :key="item">{{item}}</el-radio>
        </el-radio-group>
      </div>
    </partLayout>
    <partLayout :title="title2" @setClick="setClick">
      <div slot="layoutBody">
        <el-radio-group v-model="radio2" @change="change">
          <el-radio v-for="item in radioList2" :label="item" :key="item">{{item}}</el-radio>
        </el-radio-group>
        <div class="split-line"></div>
        <el-radio-group v-model="radio2" @change="change">
          <el-radio v-for="item in radioList22" :label="item" :key="item">{{item}}</el-radio>
        </el-radio-group>
      </div>
    </partLayout>
    <div class="right-layout-footer">
      <span class="go-detail" @click.stop="goDetail">研判详情&nbsp;>></span>
    </div>
  </div>
</template>
<script>
  import partLayout from './partLayout.vue'
  export default{
    name: 'rightLayout',
    components: {partLayout},
    props: {
      initRadio1: {
        type: String,
        default: ''
      },
      initRadio2: {
        type: String,
        default: ''
      }
    },
    data(){
      return {
        title1: '犯罪类型',
        title2: '时间周期',
        radio1: this.initRadio1 || '盗窃',
        radio2: this.initRadio2 || '2017年9月',
        radioList1: ['盗窃', '强奸', '杀人', '打架', '故意伤害', '贩卖毒品', '卖淫嫖娼', '经济犯罪', '其他'],
        radioList2: ['2017年7月', '2017年8月', '2017年9月', '2017年10月', '2017年11月', '2017年12月'],
        radioList22: ['2016', '2015'],
        value: {}
      }
    },
    methods: {
      change(){
        this.$emit('input', this.getRadioInfo())
        this.$emit('change', this.getRadioInfo())
      },
      goDetail(){
        this.$emit('input', this.getRadioInfo())
        this.$emit('goDetail', this.getRadioInfo())
      },
      setClick(){
        this.$emit('input', this.getRadioInfo())
        this.$emit('setClick', this.getRadioInfo())
      },
      getRadioInfo(){
        this.value = {radio1: this.radio1, radio2: this.radio2}
        return this.value
      }
    },
    created(){
      this.$emit('input', this.getRadioInfo())
//      this.$nextTick(()=> {
//        this.$emit('change', this.getRadioInfo())
//      })
    }
  }
</script>
<style lang="scss" scoped>
  .right-layout {
    width: 302px;
    background-color: rgba(5, 42, 84, 0.8);
    border-radius: 6px;
    border: solid 1px #4298da;
    position: relative;

  .el-radio-group {
    display: block;
  }

  .el-radio,
  .el-radio + .el-radio {
    display: block;
    margin-left: 0;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 24px;
    letter-spacing: 0px;
    color: #ffffff;
  }

  .el-radio /deep/ .el-radio__label {
    margin-left: 10px;
  }

  .el-radio /deep/ .el-radio__input.is-checked + .el-radio__label {
    color: #ffffff;
  }

  .el-radio /deep/ .el-radio__inner {
    width: 12px;
    height: 12px;
    background-color: rgba(5, 42, 84, 0.8);
  }

  .el-radio /deep/ .el-radio__input .el-radio__inner {
    border: solid 1px #ffffff;
  }

  .el-radio /deep/ .el-radio__input.is-checked .el-radio__inner {
    background: #0099e8;
    border-color: #0099e8;
  }

  }

  .split-line {
    border: dashed 1px #2ed0ff;
    opacity: 0.3;
    margin: 8px 0;
  }

  .right-layout-footer {
    display: block;
    position: relative;
    height: 30px;
  }

  .go-detail {
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 39px;
    letter-spacing: 0;
    color: #1ecdff;
    position: absolute;
    margin-right: 10px;
    bottom: 0;
    right: 0;
    cursor: pointer;
  }
</style>



