<template>
  <div class="part-layout">
    <div class="layout-title">
      <span>{{title}}</span>
      <i class="iconfont icon-shezhi" @click.stop="setClick"></i>
    </div>
    <div class="layout-body">
      <slot name="layoutBody"></slot>
    </div>
  </div>
</template>
<script>
  export default{
    name: 'partLayout',
    props: {
      title: {
        type: String,
        default: ''
      }
    },
    data(){
      return {}
    },
    methods: {
      setClick(){
        this.$emit('setClick', {})
      }
    }
  }
</script>
<style lang="scss" scoped>
  .part-layout {

  .layout-title {
    position: relative;
    width: 100%;
    height: 40px;
    background-color: rgba(28, 83, 137, 0.8);

  span {
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    line-height: 40px;
    letter-spacing: 0px;
    margin-left: 20px;
    color: #ffffff;
  }

  i {
    position: absolute;
    top: 0;
    right: 10px;
    font-size: 14px;
    line-height: 40px;
    cursor: pointer;
  }

  }

  .layout-body {
    margin: 16px 20px 12px 20px;
  }

  }
</style>



