<template>
  <div class="type-wrap">
    <div class="nav">
      <div class="links">
        <ul class="link-list">
          <router-link tag="li" v-for="item,index in items" :to="item.to" :key="index"><span>{{item.name}}</span></router-link>
        </ul>
        <jSelect style="width: 625px;margin-left: 100px;padding-top:1px"></jSelect>
      </div>
    </div>
    <div class="container">
      <div class="chart-box">
        <div class="chart-title">犯罪类型分布-刑事案件</div>
        <bar :echartsData="bar1"></bar>
      </div>
      <div class="chart-box">
        <div class="chart-title">犯罪类型分布-治安案件</div>
        <bar :echartsData="bar2"></bar>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  /******* 第三方 组件库 *****/
  /**** 本地公用变量 公用函数 **/
  /******* 本地 公用组件 *****/
  /**** 当前组件的 子组件等 ***/
  import bar from 'pages/type/children/bar.vue'
  import jSelect from '../../components/jSelect.vue';
  export default {
    name: 'person',
    data() {
      return {
        abortIndex: 2,//忽略默认项序号
        items: [
        {
          to: '/analyse/timedis',
          name: '时间分布'
        },
        {
          to: '/analyse/areas',
          name:'地域分布'
        },
        {
          to: '/analyse/type',
          name: '类型分布'
        },
        {
          to: '/analyse/person',
          name: '人员分布'}
        ],
        bar1:{
          xdata:['杀人','伤害','抢劫','强奸','拐卖妇女儿童','盗窃','诈骗','涉币', '走私','其它'],
          seriesdata:[120,160,95,124,75,158,90,112,75,50],
          barcolor:['rgba(30,221,255,0.7)','rgba(10,141,255,0.7)']
        },
        bar2:{
          xdata:['寻衅滋事','扰乱公共场所秩序','阻碍执行职务','盗窃','抢夺','诈骗','卖淫嫖娼','赌博'],
          seriesdata:[100,55,95,8,37,24,37,88],
          barcolor:['rgba(253,156,65,0.9)','rgba(249,109,0,0.9)']
        }
      }
    },
    mounted() {
      //this.echartBar1();
      //this.echartBar2();
    },
    methods: {

    },
    computed: {

    },
    components: {
      jSelect,bar
    }
  }
</script>
<style lang="scss" scoped>
  @import "../../assets/css/_mixin.scss";
  @import "../../assets/css/_variable.scss";
  /*缺角宽度*/
  $width-angle: 8px;
  /*边框长度  减去的宽度 为 width-angle 的 2 倍*/
  $width: calc(100% - 16px);

  /*边框颜色*/
  $color-border: #3780b9;

  /*box 盒子的背景色*/
  $color-background: #053767;

  /*调薪渐变颜色*/
  $color-linear: rgba(255, 255, 255, 0.008);
  /*背景样式*/
  .type-wrap {
    height: calc(100vh - 204px);
    .nav {
      padding-left: 10px;
      .links {
      .link-list {
      	vertical-align: top;
        display: inline-flex;
        justify-content: flex-start;
        align-items: center;
        li {
          color: #b2d7ff;
          font-size: 16px;
          cursor: pointer;
          width: 98px;
          height: 30px;
          padding: 0 10px;
          text-align: center;
          box-sizing: border-box;
          background-color: #143759;
          border: 1px #54b0ff solid;
          border-right: none;
          transform:skewX(-30deg);
          @include animation();
          &:nth-last-child(1) {
             border-right: 1px #54b0ff solid;
           }
          span {
            height: 30px;
            line-height: 30px;
            display: inline-block;
            transform:skewX(30deg);
          }
          &:hover, &.active {
                      color: #fff;
                      background-color: #2c72ba;
                    }
          }
        }
      }
    }
    .container {
      width: 100%;
      height: calc(100% - 70px);
      padding-top: 40px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .chart-box {
        width: 100%;
        height: calc(50% - 10px);
        box-sizing: border-box;
        padding: 20px;
        &:nth-child(-n+1) {
           margin-bottom: 20px;
        }
        .chart-title {
          width: 100%;
          font-size: 16px;
          position: relative;
        &:before {
           content: '';
           display: table-cell;
           width: 20px;
           height: 20px;
           background: url("../../assets/img/chart-label.png") no-repeat center;
           position: absolute;
           top: 0;
           left: -22px;
         }
        }
      }
      .chart-box {
        position: relative;
        background-image: linear-gradient(-45deg, $color-linear 25%, transparent 25%, transparent 50%, $color-linear 50%, $color-linear 75%, transparent 75%, transparent);
        background-size: 20px 20px;
        z-index: 1;
        &::before, &::after {
                      content: '';
                      display: block;
                      position: absolute;
                      left: 0;
                      top: 0;
                      width: 100%;
                      height: 100%;
                      z-index: -1;
        }
        &:before {
           background-color: #093462;
           background: linear-gradient(135deg, transparent 6px, $color-border 0, $color-border 7px, transparent 6px, $color-background 0) top left,
           linear-gradient(-135deg, transparent 6px, $color-border 0, $color-border 7px, $color-background 0) top right,
           linear-gradient(-45deg, transparent 6px, $color-border 0, $color-border 7px, $color-background 0) bottom right,
           linear-gradient(45deg, transparent 6px, $color-border 0, $color-border 7px, $color-background 0) bottom left;
           background-size: 51% 51%;
           background-repeat: no-repeat;
         }
        &:after {
         // background: linear-gradient(180deg, #f00 2px, transparent 0, transparent calc(100% - 2px), #f00 0) top 0 left 8px,
         //           linear-gradient(90deg, #f00 2px, transparent 0, transparent calc(100% - 2px), #f00 0) left 0 top 8px;
           background: linear-gradient(180deg, $color-border 1px, transparent 0) top 0 left $width-angle,
           linear-gradient(0deg, $color-border 1px, transparent 0) bottom 0 left $width-angle,
           linear-gradient(90deg, $color-border 1px, transparent 0) left 0 top $width-angle,
           linear-gradient(-90deg, $color-border 1px, transparent 0) right 0 top $width-angle;
           background-repeat: no-repeat;
           background-size: $width 50%, $width 50%, 50% $width, 50% $width;
         }
      }
    }
  }
</style>




