<template>
  <div ref="mapChart" class="mapChart-wrapper" style="height:calc(100% - 24px);width:100%">

  </div>
</template>

<script>
  import {chartMixin} from 'assets/js/common.js';

  export default {
    name: "socialRadar",
    mixins: [chartMixin],
    props: {
      echartsData: {
        type: Object,
        default: () => {
        }
      }
    },
    data() {
      return {}
    },
    watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.myChart = this.$echarts.init(this.$refs.mapChart);

      this.$nextTick(() => {
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let option = {
          color: ['#149be2'],
          tooltip: {
            trigger: 'item',
            backgroundColor: 'rgba(24, 47, 73, 0.9)',
            borderColor: '#167cc4',
            borderWidth: 1,
            extraCssText: 'box-shadow: 0 0 3px rgba(255, 255, 255, 0.3);',
            textStyle: {
              color: '#fff'
            },
            axisPointer: {
              type: 'shadow',
              shadowStyle: {
                color: "rgba(255,255,255, 0.1)"
              }
            },
            /*formatter: function (params) {
              console.log(params)
            }*/
          },
          grid: {
            left: '30',
            right: '20',
            bottom: '20',
            containLabel: true
          },
          radar: [
            {
              indicator: [
                {text: '经济关系',max: 500},
                {text: '宗教关系', max: 500},
                {text: '伦理道德关系', max: 500},
                {text: '法律关系', max: 500},
                {text: '政治关系', max: 500}
              ],
              center: ['50%', '50%'],
              radius: '70%',
              startAngle: 90,
              splitNumber: 4,
              shape: 'circle',
              name: {
                formatter: '{value}',
                textStyle: {
                  color: '#fff',
                  fontSize: 14,
                }
              },
              nameGap: 5,
              splitArea: {
                show: true,
                areaStyle: {
                  color: ['rgba(114, 172, 209, 0)',
                    'rgba(114, 172, 209, 0)', 'rgba(114, 172, 209, 0)',
                    'rgba(114, 172, 209, 0)', 'rgba(114, 172, 209, 0)'],
                  shadowColor: 'rgba(0, 0, 0, 0.3)',
                  shadowBlur: 10
                }
              },
              axisLine: {
                lineStyle: {
                  color: '#124c8b'
                }
              },
              splitLine: {
                lineStyle: {
                  color: '#124c8b'
                }
              },
              axisLabel: {
                show: false
              }
            }
          ],
          series: [
            {
              type: 'radar',
              data: [
                {
                  name: '社会关系属性',
                  value: [400, 380, 300, 410, 357],
                  symbol: 'circle',
                  symbolSize: 0,
                  lineStyle: {
                    normal: {
                      type: 'solid'
                    }
                  }
                }
              ],
              areaStyle: {
                normal: {
                  opacity: 0.7,
                  color: '#0a8dff'
                }
              },
            }
          ]
        }
        this.myChart.setOption(option);
      },
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
    },
  }
</script>

<style lang="scss" scoped>

</style>



