  }

  .title_name {
    font-family: '微软雅黑';
    margin-top: 12px;
    font-size: 16px;
    font-weight: bold;
    float: left;
    margin-left: 8px;
  }

  .div_hotspot {

    /*left: 20%;*/
    /*right:20%;*/
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    top: 55px;
    /*width: 1109px;*/
    width: 60%;
    height: 810px;
    position: absolute;
  }

  .map_hot {
    width: 100%;
    height: 100%;
    /*background-size: 100% 100%;*/
    /*background-size: contain;*/
    background: url('../../assets/img/hotspotbg.png') no-repeat center;
  }

  .div_btn {
    position: absolute;
    z-index: 5;
    right: 11%;
    /*left: 80%;*/
    top: 60px;
    width: 64px;
    height: 25px;
  }

  .btn_enlarge {
    /*position: relative;*/
    border: 1px solid #0994ff;
    box-sizing: border-box;
    width: 30px;
    height: 25px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background-color: rgba(5, 105, 201, .8);
    float: left;
    line-height: 22px;
    font-size: 22px;
    text-align: center;
    cursor: pointer;
  }

  .btn_enlarge:hover {
    background-color: rgba(5, 105, 201, .3);
  }

  .btn_narrow {
    border: 1px solid #0994ff;
    box-sizing: border-box;
    width: 30px;
    height: 25px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    background-color: rgba(5, 105, 201, .8);
    margin-left: 3px;
    float: left;
    line-height: 19px;
    font-size: 32px;
    text-align: center;
    cursor: pointer;
  }

  .btn_narrow:hover {
    background-color: rgba(5, 105, 201, .3);
  }

  @media screen and (max-width: 1580px) {
    .div_btn {
      right: 20%;
    }
  }

  @media screen and (max-width: 1350px) {
    .div_btn {
      right: 25%;
    }
  }

</style>



