<template>
  <div class="hot-warp">
    <div class="hot-left">
      <div class="case-num">
        <div class="case-sum">
          <div class="num">
            <div class="amount">
              <span>1</span>
              <span>2</span>
            </div>
            <div class="case-text">今日新增</div>
          </div>
          <div class="sum">
            <div class="amount">
              <span>9</span>
              <span>8</span>
              <span>0</span>
              <span>7</span>
            </div>
            <div class="case-text">案件总数</div>
          </div>
        </div>
        <div class="case-echart" id="case-echart"></div>
      </div>


      <div class="case-type table-wrap">
        <table class="table-list">
          <thead>
            <tr>
              <th width="45%">重点关注案件类型</th>
              <th width="27.5%">案件数量</th>
              <th width="27.5%">趋势</th>
            </tr>
          </thead>
          <tbody v-for="list in lists">
          <tr>
            <td>{{list.type}}</td>
            <td>{{list.num}}</td>
            <td :class="list.tendency>'上升'?'up':'down'">{{list.tendency}}</td>
          </tr>
          </tbody>
        </table>
        <span class="particulars">研判详情&nbsp;>></span>
      </div>
    </div>
    <div class="hot-center">
      <div class="hot-btn">
        <button><i class="iconfont icon-add"></i></button>
        <button><i class="iconfont icon-minus"></i></button>
      </div>
      <!-- <img src="../../assets/img/hot.jpg"/> -->
    </div>
    <div class="hot-right">
      <div class="ranking table-wrap">
        <div class="title">
          <div class="title_line1" ></div>
          <div class="title_line2"></div>
          犯罪热点派出所排名
        </div>
        <table class="table-list">
          <thead>
            <tr>
              <th width="25%">序号</th>
              <th width="37.5%">派出所</th>
              <th width="37.5%">案发数量</th>
            </tr>
          </thead>
          <tbody v-for="(item,i) in items">
            <tr>
              <td><span :class="i<=2?'yuan':'id'">{{i+1}}</span></td>
              <td :class="item.police <= 2 ?'big':'normal'">{{item.police}}</td>
              <td :class="item.num <= 2?'big':'normal'">{{item.num}}</td>
            </tr>
          </tbody>
        </table>
        <span class="particulars">研判详情&nbsp;>></span>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
export default {
  name: "crimeHot",
  data() {
    return {
      lists: [
        {
          type: "经济犯罪",
          num: "567",
          tendency: "上升"
        },
        {
          type: "故意伤害",
          num: "567",
          tendency: "下降"
        },
        {
          type: "盗窃",
          num: "567",
          tendency: "下降"
        },
        {
          type: "盗窃",
          num: "567",
          tendency: "下降"
        },
        {
          type: "经济犯罪",
          num: "567",
          tendency: "下降"
        },
        {
          type: "涉毒",
          num: "567",
          tendency: "上升"
        }
      ],
      items: [
        {
          police: "海淀分局",
          num: "678"
        },
        {
          police: "香山派出所",
          num: "678"
        },
        {
          police: "中关村西区派出所",
          num: "678"
        },
        {
          police: "紫竹院派出所",
          num: "678"
        },
        {
          police: "万寿路派出所",
          num: "678"
        },
        {
          police: "西郊机场派出所",
          num: "678"
        },
        {
          police: "农研派出所",
          num: "678"
        },
        {
          police: "万寿路派出所",
          num: "678"
        },
        {
          police: "东升派出所",
          num: "678"
        },
        {
          police: "燕园派出所",
          num: "678"
        },
        {
          police: "万寿路派出所",
          num: "678"
        },
        {
          police: "燕园派出所",
          num: "678"
        },
        {
          police: "万寿路派出所",
          num: "678"
        }
      ]
    };
  },
  methods: {
    myChart() {
      let myChart = this.$echarts.init(document.getElementById("case-echart"));
      let data = [
        "1月",
        "2月",
        "3月",
        "4月",
        "5月",
        "6月",
        "7月",
        "8月",
        "9月",
        "10月",
        "11月",
        "12月"
      ];
      let data1 = [3, 2, 5, 8, 9, 3, 3, 2, 5, 8, 9, 3, 8, 5];
      let option = {
        color: ["#3398DB"],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            show: true,
            type: "line",
            snap: true,
            lineStyle: {
              color: "#7AAEE8",
              width: 1,
              type: "dashed"
            }
          },
          formatter: function(data) {
            return (
              '<p style="color:#fcdf16;font-size:26px;text-align:center;">' +
              data[0].value +
              '<span style="font-size:16px;color:#fff;">起</span></p><span>2017年' +
              data[0].name +
              "案件数量</span>" +
              '<i style="position:absolute;bottom:-12px;left:50%;margin-left:-3px;width:0;height:0;border:6px solid transparent;border-top:6px solid #167cc4;"></i>' +
              '<i style="position:absolute;bottom:-9px;left:50%;width:0;height:0;margin-left:-2px;border:5px solid transparent;border-top:5px solid rgba(27,46,70,0.85);></i>'
            );
          },
          backgroundColor: "rgb(26,54,81)",
          borderColor: "#167cc4",
          borderWidth: 2,
          padding: 5,
          textStyle: {
            color: "#fff"
          },
          extraCssText: "box-shadow:0 0 10px 0 rgba(40,157,252,.6)"
        },
        grid: {
          show: true,
          left: "3%",
          right: "4%",
          bottom: "9%",
          top: "14%",
          containLabel: true,
          borderWidth: 0,
          backgroundColor: {
            type: "linear",
            x: 0,
            y: 0,
            x2: 0,
            y2: 1,
            colorStops: [
              {
                offset: 0,
                color: "rgba(13,53,104,.5)" // 0% 处的颜色
              },
              {
                offset: 1,
                color: "rgba(40, 82, 181, .5)" // 100% 处的颜色
              }
            ],
            globalCoord: false // 缺省为 false
          }
        },
        xAxis: [
          {
            type: "category",
            data: data,
            boundaryGap: true, //坐标轴的留白
            axisLine: {
              //隐藏X轴线
              show: true,
              lineStyle: {
                color: "#0592EA"
              }
            },
            axisTick: {
              show: false //坐标轴刻度
            },
            axisLabel: {
              textStyle: {
                color: "#fff",
                fontSize: 12
              }
            }
          }
        ],
        yAxis: {
          type: "value",
          name: "",
          axisTick: {
            show: false //坐标轴刻度
          },
          axisLine: {
            show: false
          },
          axisLabel: {
            textStyle: {
              color: "#fff",
              fontSize: 12
            }
          },
          splitLine: {
            lineStyle: {
              color: "#166DD4",
              type: "dashed"
            }
          }
        },
        series: [
          {
            name: "",
            type: "line",
            showSymbol: false,
            barWidth: "60%",
            smooth: true,
            symbolSize: 1,
            itemStyle: {
              normal: {
                color: "#E8E212"
              },
              emphasis: {
                opacity: 1
              }
            },
            data: data1
          }
        ]
      };
      myChart.setOption(option);
    }
  },
  mounted() {
    this.myChart();
  }
};
</script>
<style lang="scss" scoped>
.hot-warp {
  display: flex;
  .hot-left {
    width: 400px;
    margin-bottom:20px;
    padding-bottom:20px;
    .case-num {
      height: 326px;
      background: rgb(5, 54, 102);
      border: 1px solid #3780b9;
      border-radius: 6px;
      .case-sum {
        display: flex;
        width: 70%;
        padding: 0 15%;
        .amount {
          text-align: center;
        }
        .case-text {
          text-align: center;
          margin: 12px 0 25px 0;
        }
        .num {
          width: 50%;
          margin-top: 40px;
          span {
            padding: 0px 4px;
            background: #fcdd16;
            font-size: 20px;
            color: #000;
            font-weight: bold;
            border: 1px solid #ffe400;
          }
        }
        .sum {
          width: 50%;
          margin-top: 40px;
          span {
            padding: 0px 4px;
            background: #1daad2;
            font-size: 20px;
            color: #000;
            font-weight: bold;
            border: 1px solid #00aeff;
          }
        }
      }
      .case-echart {
        height: 172px;
      }
    }
    .case-type {
      height: 363px;
      background: rgb(5, 53, 101);
      margin-top: 30px;
      border: 1px solid #3780b9;
      border-radius: 6px;
    }
  }
  .hot-center {
    width: 62%;
    margin-left: 4px;
    position: relative;
    background: url("../../assets/img/hot.jpg") no-repeat 50%;
    background-size: cover;
    // img {
    //   width: 105%;
    //   height: 105%;
    // }
    .hot-btn {
      position: absolute;
      right: 37px;
      top: 23px;
      button {
        width: 30px;
        height: 30px;
        background: #0671d0;
        outline: none;
        border: 1px solid #0994ff;
        border-radius: 6px;
        color: #fff;
        font-size: 16px;
      }
    }
  }
  .hot-right {
    width: 396px;
    margin-bottom:20px;
    padding-bottom:20px;
    // position: relative;
    .ranking {
      height: 720px;
      border: 1px solid #3780b9;
      background: rgba(5, 55, 103, 0.6);
      border-radius: 6px;
      .title {
        display: flex;
        height: 55px;
        line-height: 55px;
        font-size: 16px;
        i {
          font-size: 16px;
          margin: 0 3px 0 -3px;
        }
      }
    }
  }
  .table-wrap {
    width: 100%;
    .table-list {
      width: 100%;
      color: #fff;
      tr,
      th {
        // height: 40px;
        // line-height: 40px;
        box-sizing: border-box;
        text-align: center;
      }
      tr {
        height: 40px;
        line-height: 20px;
        margin: 0 20px;
        transition: all 0.2s ease-in;
        &:hover {
          background-color: #0352a0;
        }
        td {
          border-bottom: 1px #116391 dashed;
        }
      }
      thead {
        background: #174d82;
      }
    }
  }
  .particulars {
    display: block;
    margin: 30px 4% 10px 0;
    color: #1ecdff;
    font-size: 14px;
    text-align: right;
    cursor: pointer;
  }
  .up {
    color: #23c895;
  }
  .down {
    color: #f55870;
  }
  .yuan {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: rgba(255, 35, 61, 0.8);
    border-radius: 50%;
  }
  .big {
    font-weight: bold;
  }
  .title_line1 {
    margin-top: 25px;
    background-color: white;
    width: 17px;
    height: 3px;
    box-shadow: 0 0 10px #ffffff;
  }
  .title_line2 {
    margin: 30px 15px 0 -17px;
    background-color: white;
    width: 11px;
    height: 1px;
    box-shadow: 0 0 10px #ffffff;
  }
}
</style>
<style>
.container-wrapper{
  margin: 20px auto 0px !important;
  padding:20px 30px 0px !important;
}
</style>



