<template>
  <div id="app" ref="app" class="main-wrapper">
    <div class="main" :class="{'add-bg': !loginheader}">
      <HeaderWrap v-if="loginheader"></HeaderWrap>
      <loginHeader v-if="!loginheader"></loginHeader>
      <transition name="route-change">
        <containers>
          <router-view/>
        </containers>
      </transition>
    </div>
    <footerwrap :isLoginFooter="!loginheader"></footerwrap>
  </div>
</template>

<script>
  import HeaderWrap from 'components/header.vue'
  import loginHeader from 'components/loginheader';
  import footerwrap from 'components/footer';
  import containers from 'components/container.vue';
  import {mapGetters} from 'vuex'

  export default {
    name: 'app',
    data() {
      return {
        //通过登录状态确定是否渲染header
        loginheader: true
      }
    },
    //导入getters取得state放入计算属性中
    computed: {

    },
    created() {
      if (this.$route.name === 'login') {
        this.isShow = false
        this.loginheader = false
      }
      if (this.$route.name === 'index') {
        this.isShow = false
      }
    },
    mounted() {

    },
    watch: {
      // 监视路由变化
      $route(newVal, oldVal) {
        if (newVal.name === 'login') {
          this.loginheader = false
        } else if (newVal.name === 'index') {
          this.loginheader = true
        } else {
          this.loginheader = true
        }
      }
    },
    methods: {

    },

    components: {
      HeaderWrap,
      footerwrap,
      containers,
      loginHeader
    }
  }
</script>

<style lang="scss" scoped>
  @import "./assets/css/_variable.scss";

  .main-wrapper {
    height: 100%;
    min-height: 100%;
    background: #0b2037;
    overflow: auto;
    .main {
      box-sizing: border-box;
      min-height: 100%;
      min-width: 1200px;
      padding-bottom: 64px;
      .route-change-enter-active {
        transition: all .3s ease-in;
      }
    }
    .add-bg {
      background-image: url("./assets/img/login_bg.jpg");
      background-size: cover;
    }
  }


</style>



