<template>
	<div class="chart-wrapper" ref="chart" style="height:100%;width:100%">

	</div>
</template>

<script>
	import {chartMixin} from 'assets/js/common'
	export default {
		name: 'bar',
		mixins: [chartMixin],
		props: {
			criminalActMonthData: {
				type: Object,
				default: () => {}
			}
		},
		data() {
			return {}
		},
		watch: {
			criminalActMonthData: {
				handler: function(val, oldVal) {
					this.initChart()
				},
				deep: true //增加deep 观察对象的子对象变化
			}
		},
		mounted() {
			this.$nextTick(() => {
//				console.log(this.criminalActMonthData)
				this.myChart = this.$echarts.init(this.$refs.chart);
				this.initChart()
			})
		},
		methods: {
			initChart() {
				let option = {
					color: ['#3398DB'],
					tooltip: {
						padding:[20,25],
						backgroundColor:"rgba(27,46,70,0.85)",
						borderColor:"#167cc4",
						borderWidth:"1",
						trigger: 'axis',
						axisPointer: {
							show: true,
							type: 'shadow',
							snap: true,
						},
						textStyle:{
							color:"#fff",
						},
						formatter:(params)=>{
							let html = '<div>'
							params.forEach((val,index)=>{
								html+= `${val.seriesName}:<span style="font-size:18px">&nbsp;${val.value}</span><br/>`
							})
							return html+'</div>'
						},
						extraCssText: 'box-shadow: 0 0 3px 1px rgba(255, 255, 255, 0.3);'
					},
					grid: {
						left: '3%',
						right: '3%',
						bottom: '6%',
						top: "14%",
						show:true,
			            backgroundColor:new this.$echarts.graphic.LinearGradient(0,0, 0, 1, [{
			              offset: 0.5,
			              color: 'rgba(40,82,181,0)'
			            }, {
			              offset: 1,
			              color: 'rgba(40,82,181,0.6)'
			            }]),
						containLabel: true,
						borderWidth:0
					},
					xAxis: {
						type: 'category',
						data: this.criminalActMonthData.time,
//					    nameGap:24,
			            boundaryGap: true,   //坐标轴的留白
			            axisLine: {      //隐藏X轴线
			              show: true,
			              lineStyle: {
			                color: "#0a68a8",
			              }
			            },
			            axisTick:{
							show:false //坐标轴刻度
						},
			            axisLabel: {
			            	margin:14,
							textStyle: {
								color: '#fff',
								fontSize: 14,
							},
						},
					},
					yAxis: {
						type: 'value',
						name: '',
						axisTick:{
							show:false //坐标轴刻度
						},
						axisLine: {
							show: false,
						},
						axisLabel: {
							textStyle: {
								color: '#fff',
								fontSize: 14,
							},
						},
						splitLine: {
							lineStyle: {
								color: '#166DD4',
								type: "dashed",

							}
						},
					},
					legend: {
						data: ["已侦破", "未侦破"],
						itemWidth: 14,
						itemHeight: 9,
						itemGap: 28,
						icon: "rect",
						textStyle: {
							color: "#fff",
							fontSize: 12,
						},
					},
					series: [{
							name: '已侦破',
							type: 'bar',
							showSymbol:false,
							barWidth: '14',
							smooth: true,
							itemStyle: {
								normal: {
									color: {
									    type: 'linear',
									    x: 0,
									    y: 0,
									    x2: 0,
									    y2: 1,
									    colorStops: [{
									        offset: 0, color: 'rgba(30,221,255,0.71)' // 0% 处的颜色
									    }, {
									        offset: 1, color: 'rgba(10,141,255,0.71)' // 100% 处的颜色
									    }],
									},
									borderColor:"#08b1ff",
								},
								emphasis: {
									opacity: 1
								}
							},
							data: this.criminalActMonthData.suspiciousperson
						},
						{
							name: '未侦破',
							type: 'bar',
							smooth: true,
							showSymbol:false,
							barWidth: '14',
							itemStyle: {
								normal: {
									color:  {
									    type: 'linear',
									    x: 0,
									    y: 0,
									    x2: 0,
									    y2: 1,
									    colorStops: [{
									        offset: 0, color: 'rgba(253,156,65,0.9)' // 0% 处的颜色
									    }, {
									        offset: 1, color: 'rgba(249,109,0,0.9)' // 100% 处的颜色
									    }],
									},
									borderColor:"#f99200",
								},
								emphasis: {
									opacity: 1
								}
							},
							data: this.criminalActMonthData.suspiciousgoods
						},
					]
				}
				this.myChart.setOption(option);

			},
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
		},
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



