<template>
  <div class="box">
    <div class="mapBox">
      <rightLayout class="fr" @change="change"></rightLayout>
      <div class="hot-btn">
        <button>平面地图</button>
        <button>影像图</button>
        <button><i class="iconfont icon-add"></i></button>
        <button><i class="iconfont icon-minus"></i></button>
      </div>
    </div>
    <div class="bottom-table-box">
        <table class="table">
          <thead>
          <tr>
            <th v-for="item in thead">{{item}}</th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="item,index in list">
            <td>{{index+1}}</td>
            <td>{{item.col1}}</td>
            <td>{{item.col2}}</td>
            <td :class="item.col3>0?'red':'green'">{{item.col3}}%</td>
            <td>{{item.col4}} <span :class="item.col5>0?'red':'green'">({{item.col5}}%)</span></td>
            <td>{{item.col6}}</td>
            <td :class="item.col12>0?'red':'green'">{{item.col12}}%</td>
            <td>{{item.col8}}<span  :class="item.col9>0?'red':'green'">({{item.col9}}%)</span></td>
            <td>{{item.col11}}</td>
            <td :class="item.col12>0?'red':'green'">{{item.col12}}%</td>
            <td>{{item.col13}}</td>
          </tr>
          </tbody>
        </table>

    </div>
  </div>
</template>
<script type="text/javascript">
  import rightLayout from '../../components/rightLayout.vue'
  export default {
    name: 'gridding',
    data() {
      return {
        thead: ['网格编号', '网格辖区社区', '盗窃', '同期环比', '下周期预测', '总案件', '同期环比', '下周期预测', '警力部署', '同期环比', '建议部署'],
        list: [
          {
            col1: "海淀区王庄路街道/海淀区清华东路街道幸福社区",
            col2: 14,
            col3: '+12',
            col4: 18,
            col5: '+12',
            col6: 97,
            col7: 23,
            col8: 23,
            col9: '-30',
            col10: '23(-30%)',
            col11: 12,
            col12: '-30',
            col13: '小幅增加'
          },
          {
            col1: "海淀区保福寺桥街道/海淀区中关村街道和谐社区",
            col2: 35,
            col3: '+13',
            col4: 34,
            col5: '-40',
            col6: 78,
            col7: 28,
            col8: 28,
            col9: '+13',
            col10: '18(+12%)',
            col11: 31,
            col12: '-20',
            col13: '维持不变'
          },
          {
            col1: "海淀区苏州桥街道/海淀区远大路街道富强社区",
            col2: 22,
            col3: '-30',
            col4: 23,
            col5: '-30',
            col6: 232,
            col7: 34,
            col8: 34,
            col9: '+13',
            col10: '34(+13%)',
            col11: 15,
            col12: '+12',
            col13: '大幅增加'
          },
          {
            col1: "海淀区苏州桥街道/海淀区远大路街道社区",
            col2: 41,
            col3: '-20',
            col4: 53,
            col5: '-20',
            col6: 71,
            col7: 23,
            col8: 23,
            col9: '-30',
            col10: '23(-30%)',
            col11: 16,
            col12: '-30',
            col13:'大幅增加'
          }
        ]
      }
    },
    mounted() {

    },
    methods: {
      change(info){
//        console.info(info)
      }
    },
    components: {
      rightLayout
    }
  }

</script>
<style lang="scss" scoped>
  .mapBox {
    width: 100%;
    height: 625px;
    background: url("../../assets/img/mapBag.jpg") no-repeat;
    position: relative;
    .hot-btn {
      position: absolute;
      right: 350px;
      top: 23px;
      button {
        padding: 2px 10px;
        background: #0671d0;
        outline: none;
        border: 1px solid #0994ff;
        color: #cee5f9;
        font-size: 16px;
      }
      button:nth-child(1),button:nth-child(2){
        border-radius: 6px;
      }
      button:nth-child(3){
        border-top-left-radius: 6px;
        border-bottom-left-radius: 6px;
      }
      button:nth-child(4){
        border-top-right-radius: 6px;
        border-bottom-right-radius: 6px;
      }
    }
  }

  .bottom-table-box {
    background-color: #053360;
    border: 1px solid #3985c1;
    border-radius:8px;
    overflow: hidden;
  }

  .table {
    width: 100%;
    /*height: 300px;*/
    text-align: center;
    thead {
      height: 42px;
      line-height: 42px;
      background-color: #174d82;
      th {
        text-align: center;
        font-size: 14px;
        font-weight:500;
      }
    }
    tbody {
      width: 100%;
      tr:last-child {
        border: none;
      }
      tr{
        height:40px;
        line-height:40px;
        border-bottom: 1px dashed #3985c1;
        td {
          text-align: center;
        }
      }
      tr:hover{
        background-color: #0352a0;
      }
    }
  }
  .red{
    color:#f55870;
  }
  .green{
    color: #23c895;
  }
</style>



