<template>
  <div class="chart-wrapper" ref="chart" style="height:calc(100% - 24px);width:100%">

  </div>
</template>

<script>
  import {chartMixin} from 'assets/js/common'

  export default {
    name: 'genderPie',
    mixins: [chartMixin],
    props: {
      echartsData: {
        type: Object,
        default: () => {
        }
      }
    },
    data() {
      return {}
    },
    watch: {
      echartsData: {
        handler: function (val, oldVal) {
          this.initChart()
        },
        deep: true  //增加deep 观察对象的子对象变化
      }
    },
    mounted() {
      this.$nextTick(() => {
        this.myChart = this.$echarts.init(this.$refs.chart);
        this.initChart()
      })
    },
    methods: {
      initChart() {
        let option = {
          color: ['#3398DB'],
          tooltip: {
            show: true,
            formatter: function (params) {
              var html = '';
              return html = `<div><p style="font-size:26px;font-weight:bold;margin-bottom:0px;text-align:center;width:74px;color:#fff;">${params.percent}<span style="color:#fff;font-size:12px;">%</span></p>
                            <p style="text-align: center;font-size: 12px">${params.name}</p></div>`
            },
            // position: ['38%', '35%'],
            backgroundColor: 'none',
            position: function (point, params, dom, rect, size) {
              return [size.viewSize[0]/2-42, '43%']
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },

          series: [
            {
              // 外圈
              z: 0,
              name: '',
              type: 'pie',
              hoverAnimation: false,
              center: ['50%', '50%'],
              radius: ['82%', '84%'],
              label: {
                normal: {
                  position: 'center',
                  show: false
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [{
                value: 0,
                itemStyle: {
                  normal: {
                    color: '#1448ad' //颜色填充
                  },
                  emphasis: {
                    color: '#e7be14',
                  }
                },
                tooltip: {show: false}
              }]
            },
            {
              // 内圈
              z: 0,
              name: '',
              type: 'pie',
              hoverAnimation: false,
              center: ['50%', '50%'],
              radius: ['45%', '47%'],
              label: {
                normal: {
                  position: 'center',
                  show: false
                }
              },
              labelLine: {
                normal: {
                  show: false
                }
              },
              data: [{
                value: 0,
                itemStyle: {
                  normal: {
                    color: '#2c6cf1' //颜色填充
                  },
                  emphasis: {
                    color: '#e7be14',
                  }
                },
                tooltip: {show: false}
              }]
            },
            {
              name: '性别',
              type: 'pie',
              radius: ['50%', '75%'],
              center: ['50%', '50%'],
              data: [{
                value: 50,
                name: '男'
              }, {
                value: 20,
                name: '女'
              }],
              itemStyle: {
                normal: {
                  borderColor: 'rgba(9, 31, 85, 1)',
                  borderWidth: 2,

                },
                emphasis: {
                  color: '#e7be14',
                }
              },
              label: {
                normal: {
                  color: '#ffbc1c',
                  fontSize: 14,
                  position: 'outside',
                  show: true,
                  formatter: function (params) {
                    return `${params.name} ${params.percent}% ${params.value}`
                  }
                }
              },
              labelLine: {
                normal: {
                  length: 20,
                  length2: 20,
                  lineStyle: {
                    color: '#fff',
                    opacity: 1
                  }
                }
              }
            }
          ]
        }

        this.myChart.setOption(option);
      },
      // echats 图表自适应
      _windowResizeHandler() {
        this.myChart.resize()
      },
      // 销毁echats图表方法
      _destroyEchart() {
        if (this.myChart) {
          this.myChart.dispose()
        }
      }
    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



