import axios from "axios";
// axios 配置
// axios.defaults.timeout = config.TIMEOUT;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.baseURL = '';
// const baseurl = 'http://result.eolinker.com/8RiESwDd5a5506d56f9453f947d0ff45a1b5e487a9b6182?uri'
const baseurl = 'https://result.eolinker.com/8RiESwDd5a5506d56f9453f947d0ff45a1b5e487a9b6182?uri='
const baseurlMock = ''
// axios.defaults.withCredentials = true;
const baseURL = {
  dev: '',
  mock: '',
  prod: '/'
}

function getWholeUrl(options) {
  let baseUri = baseurl
  let type = Object.prototype.toString.call(options)
  let wholeUrl = ''
  if (type === '[object Object]') {
    let url = options.url || ''
    if (options.baseUri) {
      baseUri = options.baseUri
    }
    wholeUrl = baseUri + url
  } else if (type === '[object String]') {
    wholeUrl = baseUri + options
  }
  return wholeUrl
}
export function post(options, params) {
  let wholeUrl = getWholeUrl(options)

  return new Promise((resolve, reject) => {
    axios.post(wholeUrl, params)
      .then(response => {
        if (response.data.code != '501') {

          resolve(response.data);
        } else if (response.data.code == '501') {
          axios.get(baseurlMock + url, params)
            .then(response => {
              resolve(response.data);
            })
            .catch((error) => {
              reject(error)
            })
        }

      })
      .catch((error) => {
        axios.get(baseurlMock + url, params)
          .then(response => {
            resolve(response.data);
          })
          .catch((error) => {
            reject(error)
          })
      })
  })
}
export function get(options, params) {
  let wholeUrl = getWholeUrl(options)

  return new Promise((resolve, reject) => {
    axios.get(wholeUrl, params)
      .then(response => {
        if (response.data.code != "501") {
          resolve(response.data);
        } else if (response.data.code == '501') {
          axios.get(baseurlMock + url, params)
            .then(response => {
              resolve(response.data);
            })
            .catch((error) => {
              reject(error)
            })
        }

      })
      .catch((error) => {
        axios.get(baseurlMock + url, params)
          .then(response => {
            resolve(response.data);
          })
          .catch((error) => {
            reject(error)
          })
      })
  })
}



