<template>
    <div class="header-wrap">
      <div class="header-info fl">
        <router-link tag="li" :to="{name: 'index'}">
          <h1 class="header-title fl"  title="首页">猎鲨大数据</h1>
        </router-link>
        <div class="nav-info fl">
          <ul class="nav-list">
            <!-- tag="li" 可以设定 router-link生成什么样的页面元素 -->
            <router-link class="item setting" tag="li" :to="{name: 'analyse'}" @mouseover.native='showList1' @mouseout.native="hideList1">
              <i class="iconfont icon-shujufenxi"></i>
              <span>犯罪行为大数据分析</span>
              <div class="set-list" :class="{'show-set-list': isShowList1}">
                <ul @click = 'hideList1'>
                  <router-link tag="li" to="/analyse/timedis">按时间分析</router-link>
                  <router-link tag="li" to="/analyse/areas">按地域分析</router-link>
                  <router-link tag="li" to="/analyse/type">按类型分析</router-link>
                  <router-link tag="li" to="/analyse/person">按人员分析</router-link>
                </ul>
              </div>
            </router-link>
            <router-link class="item setting" tag="li" :to="{name: 'commitBigData'}" @mouseover.native='showList2' @mouseout.native="hideList2">
              <i class="iconfont icon-fanzuihangweidashujuyanpan"></i>
              <span>犯罪行为大数据研判</span>
              <div class="set-list" :class="{'show-set-list': isShowList2}">
                <ul @click = 'hideList2'>
                  <router-link tag="li" to="/commitBigData/crimeHot">犯罪热点研判</router-link>
                  <router-link tag="li" to="/commitBigData/gridding">网格化研判</router-link>
                </ul>
              </div>
            </router-link>
          </ul>
        </div>
        <div class="user-info fr">
          <div>
            <i class="iconfont icon-user user-icon"></i>
            <span class="user">王警官</span>
            <i class="iconfont arrow icon-zelvxuanzefeiyongdaosanjiaoxingfandui"></i>
          </div>
          <router-link tag="i" :to="{name: 'login'}">
            <i class="iconfont icon-logout logout" @click="logout" title="登出"></i>
          </router-link>
        </div>
      </div>
    </div>
</template>

<script type="text/javascript">
  export default {
    name: 'headers',
    data() {
      return {
        isShowList1: false,
        isShowList2: false
      }
    },
    props: {
      isWidth: {
        type: Boolean,
        default: true
      }
    },
    watch: {

    },
    methods: {
      logout() {
      //  清楚cookie session
        sessionStorage.removeItem('accessToken');
      },
      showList1() {
        this.isShowList1 = true
        console.log('click')
      },
      hideList1() {
        this.isShowList1 = false
      },
      showList2() {
        this.isShowList2 = true
        console.log('click')
      },
      hideList2() {
        this.isShowList2 = false
      }
    },

  }
</script>

<style lang="scss" scoped>
  @import "../assets/css/variable";
  @import "../assets/css/mixin";
  .header-wrap{
    width: 100%;
    min-width: $site-width;
    height: 60px;
    border-bottom: 2px solid #3c689a;
    margin: 0 auto;
    box-sizing: border-box;
    /*background-color: #0643dc;*/
    background: #0f2f53;
    background-image: url("../assets/img/header-bg_01.jpg");
    background-repeat: no-repeat;
    .header-info {
      color: #fff;
      width: 100%;
      height: 58px;
      margin: 0 auto;
      padding: 0 30px;
      align-items: center;
      box-sizing: border-box;
      .header-title {
        display: inline-block;
        width: 290px;
        font-size: 26px;
        font-weight: bolder;
        letter-spacing: 3px;
        text-shadow:3px 1px 1px #35afe5;
        line-height: 58px;
        cursor: pointer;
        position: relative;
        z-index: 111;
      }
  .user-info{
    width: 200px;
    background-image: url("../assets/img/header-bg_03.jpg");
    text-align: right;
  }
  .user-info > div:hover {
        & > .iconfont {
          color: #fff !important;
        }

      }
  .nav-info {
    width: 600px;
    display: inline-block;
    line-height: 57px;
    box-sizing: border-box;
  /*background-color: #053bbf;*/
        & .nav-list{
            width: 100%;
            display: flex;
            justify-content: flex-start;
            margin: 0 auto;
        li{
          color: $cl-txt-w;
          font-size: 16px;
          padding: 0 20px;
          margin-right: 46px;
          cursor: pointer;
        @include animation();
        &.active, &:hover{
                     background-color: #164778;
        & i {
            color: #f2dd20;
          }
        }
        }
        .setting {
          position: relative;
        .set-list {
          width: 100%;
          visibility: hidden;
          opacity: 0;
          position: absolute;
          left: 0;
          top: 60px;
          z-index: -1;
          padding: 18px 0;
          background-color: rgba(22 ,71, 120, .8);
        @include animation();
          li{
            font-size: 14px;
            width: 100%;
            padding: 0;
            text-indent: 40px;
            line-height: 28px;
              &.active, &:hover{
              background: #0071ab;
               color: #ffe400;
             }
          }
        }
        .show-set-list {
          visibility: visible;
          z-index: 100;
          opacity: 1;
        }
        }
        }
        }
      .logout {
        margin-left: 20px;
        line-height: 58px;
        color: #4d87c9;
        &:hover {
          color: #fff;
        }
      }
      i, span {
        cursor: pointer;
      }
      .user-info > div {
        display: inline-block;
        .iconfont {
          color: #5397ec;
          &:hover {
            color: #fff;
          }
        }
        .arrow {
          line-height: 14px;
          font-size: $fz-st !important;
        }
      }
    }


  }

</style>




