<template>
  <div class="container-wrapper">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: "containers",
    props: {
      show: {
        type: Boolean,
        default: true
      }
    }
  }
</script>

<style lang="scss" scoped>

  .container-wrapper {
    color: #fff;
    width: 100%;
    height: calc(100vh - 124px);
    margin: auto;
    padding: 40px 30px;
    overflow-y: scroll;
    box-sizing: border-box;
    border-radius: 10px;
  }
</style>



