<template>
  <div class="person-wrap">
    <div class="nav">
      <div class="links">
        <ul class="link-list">
          <router-link tag="li" v-for="item,index in items" :to="item.to" :key="index"><span>{{item.name}}</span>
          </router-link>
        </ul>
        <div>
          <jSelect :abortIndex="abortIndex" style="width: 404px;margin-left: 104px"></jSelect>
        </div>
        <div class="help-layout"></div>
        <div class="export-btn">
          <i class="iconfont icon-daochu" ></i>
          <span>导出</span>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="chart-box">
        <div class="chart-title">犯罪行为人员分布-籍贯</div>
        <nativePlaceBar :echartsData="nativePlaceData"></nativePlaceBar>
      </div>
      <div class="chart-box">
        <div class="chart-title">犯罪行为人员分布-性别</div>
        <genderPie></genderPie>
      </div>
      <div class="chart-box">
        <div class="chart-title">犯罪行为人员分布-社会关系属性</div>
        <socialRadar :echartsData="socialData"></socialRadar>
      </div>
      <div class="chart-box">
        <div class="chart-title">犯罪行为人员分布-年龄</div>
        <ageBar :echartsData="ageBarData"></ageBar>
      </div>
    </div>
  </div>
</template>

<script type="text/javascript">
  /******* 第三方 组件库 *****/
  /**** 本地公用变量 公用函数 **/
  /******* 本地 公用组件 *****/
  /**** 当前组件的 子组件等 ***/
  import nativePlaceBar from 'pages/person/charts/nativePlaceBar.vue';
  import genderPie from 'pages/person/charts/genderPie.vue';
  import socialRadar from 'pages/person/charts/socialRadar.vue';
  import ageBar from 'pages/person/charts/ageBar.vue';
  import jSelect from '../../components/jSelect.vue';

  export default {
    name: 'person',
    data() {
      return {
        abortIndex: 2,//忽略默认项序号
        items: [{
          to: '/analyse/timedis',
          name: '时间分布'
        }, {
          to: '/analyse/areas',
          name: '地域分布'
        }, {
          to: '/analyse/type',
          name: '类型分布'
        }, {
          to: '/analyse/person',
          name: '人员分布'
        }],
        nativePlaceData: {
          xdata: ['北京', '上海', '深圳', '重庆', '河北', '湖南', '安徽', '云南', '广西', '新疆'],
          data: [155, 20, 25, 110, 40, 10, 15, 30, 9, 35]
        },
        ageBarData: {
          xdata: ['<14', '14-18', '18-28', '28-35', '35-40', '40-48', '48-55', '55-65', '65-72', '72>'],
          data: [40, 114, 120, 126, 119, 110, 100, 80, 66, 35]
        },
        socialData: {
          data: [100, 8, 0.40, -80, 2000]
        }
      }
    },
    components: {
      nativePlaceBar,
      genderPie,
      socialRadar,
      ageBar,
      jSelect
    }
  }
</script>

<style lang="scss" scoped>
  @import "../../assets/css/_mixin.scss";
  @import "../../assets/css/_variable.scss";
  // 缺角宽度
  $width-angle: 8px;
  // 边框长度  减去的宽度 为 width-angle 的 2 倍
  $width: calc(100% - 16px);

  // 边框颜色
  $color-border: #3780b9;

  // box 盒子的背景色
  $color-background: #053767;

  // 调薪渐变颜色
  $color-linear: rgba(255, 255, 255, 0.008);
  // rgba(255, 255, 255, 0.02)
  // 背景样式
  .person-wrap {
    /*min-width: 1200px;*/
    height: calc(100vh - 204px);
    .nav {
      padding-left: 10px;
      .links {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .help-layout {
          flex: 1;
        }
        .link-list {
          display: inline-flex;
          justify-content: flex-start;
          align-items: center;
          li {
            color: #b2d7ff;
            font-size: $fz-md;
            cursor: pointer;
            width: 98px;
            height: 30px;
            padding: 0 10px;
            text-align: center;
            box-sizing: border-box;
            background-color: #143759;
            border: 1px #54b0ff solid;
            border-right: none;
            transform: skewX(-30deg);
            @include animation();
            &:nth-last-child(1) {
              border-right: 1px #54b0ff solid;
            }
            span {
              height: 30px;
              line-height: 30px;
              display: inline-block;
              transform: skewX(30deg);
            }
            &:hover, &.active {
              color: #fff;
              background-color: #2c72ba;
            }
          }
        }
        .export-btn {
          color: #ffe68f;
          text-align: center;
          font-size: $fz-sm;
          cursor: pointer;
          width: 80px;
          height: 24px;
          background-color: rgba(238, 162, 0, .2);
          border: 1px #bc8a13 solid;
          border-radius: 4px;
          @include animation();
          &:hover {
            color: #fff;
            background-color: rgba(238, 162, 0, 1);
          }
        }
      }
    }
    .container {
      width: 100%;
      height: calc(100% - 70px);
      padding-top: 40px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .chart-box {
        width: calc(50% - 10px);
        height: calc(50% - 10px);
        box-sizing: border-box;
        padding: 20px;
        &:nth-child(-n+2) {
          margin-bottom: 20px;
        }
        .chart-title {
          width: 100%;
          font-size: $fz-md;
          position: relative;
          &:before {
            content: '';
            display: table-cell;
            width: 20px;
            height: 20px;
            background: url("../../assets/img/chart-label.png") no-repeat center;
            position: absolute;
            top: 0;
            left: -22px;
          }
        }
      }
      .chart-box {
        position: relative;
        background-image: linear-gradient(-45deg, $color-linear 25%, transparent 25%, transparent 50%, $color-linear 50%, $color-linear 75%, transparent 75%, transparent);
        background-size: 20px 20px;
        z-index: 1;
        &::before, &::after {
          content: '';
          display: block;
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          z-index: -1;
        }
        &:before {
          background-color: #093462;
          background: linear-gradient(135deg, transparent 6px, $color-border 0, $color-border 7px, transparent 6px, $color-background 0) top left,
          linear-gradient(-135deg, transparent 6px, $color-border 0, $color-border 7px, $color-background 0) top right,
          linear-gradient(-45deg, transparent 6px, $color-border 0, $color-border 7px, $color-background 0) bottom right,
          linear-gradient(45deg, transparent 6px, $color-border 0, $color-border 7px, $color-background 0) bottom left;
          background-size: 51% 51%;
          background-repeat: no-repeat;
        }
        &:after {
          // background: linear-gradient(180deg, #f00 2px, transparent 0, transparent calc(100% - 2px), #f00 0) top 0 left 8px,
          //           linear-gradient(90deg, #f00 2px, transparent 0, transparent calc(100% - 2px), #f00 0) left 0 top 8px;
          background: linear-gradient(180deg, $color-border 1px, transparent 0) top 0 left $width-angle,
          linear-gradient(0deg, $color-border 1px, transparent 0) bottom 0 left $width-angle,
          linear-gradient(90deg, $color-border 1px, transparent 0) left 0 top $width-angle,
          linear-gradient(-90deg, $color-border 1px, transparent 0) right 0 top $width-angle;
          background-repeat: no-repeat;
          background-size: $width 50%, $width 50%, 50% $width, 50% $width;
        }
      }
    }
  }
</style>



