<template>
  <div class="commit-wrap">
    <div class="commit-top">
      <ul class="commit-nav">
        <router-link tag="li" to="/commitBigData/crimeHot"><span>犯罪热点研判</span></router-link>
        <router-link tag="li" to="/commitBigData/gridding"><span>网格化研判</span></router-link>
      </ul>
      <jSelect v-model="val" @change="change" v-if="isShow" :abortIndex="abortIndex" style="width: 500px;margin-left: 100px"></jSelect>
      <div class="edit" v-if="isShow2">
        <img src="../../assets/img/tubiao.png"/>
        <span>网络切分编辑</span>
      </div>
    </div>
    <router-view ref="crimeHolder"></router-view>
  </div>
</template>

<script>
import jSelect from "../../components/jSelect.vue";

export default {
  name: "commitBigData",
  components: { jSelect },
  data() {
    return {
      abortIndex: 1,
      val: {}
    };
  },
  computed: {
    isShow() {
      return this.$route.name === "crimeHot";
    },
    isShow2() {
      return this.$route.name === "gridding";
    }
  },
  methods: {
    change(val) {
      this.$refs.crimeHolder.myChart(val);
    }
  }
};
</script>

<style lang="scss" scoped>
  .commit-wrap {
.commit-top{
  padding-left:10px;
  font-size:16px;
  .commit-nav {
    display: inline-flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 30px;

  li {
    color: #b2d7ff;
    cursor: pointer;
    width: 168px;
    height: 30px;
    padding: 0 10px;
    text-align: center;
    box-sizing: border-box;
    background-color: #143759;
    border: 1px #54b0ff solid;
    border-right: none;
    transform: skewX(-30deg);

  &:nth-last-child(1) {
    border-right: 1px #54b0ff solid;
  }

  span {
    height: 30px;
    line-height: 30px;
    display: inline-block;
    transform: skewX(30deg);
  }

  &:hover,  &.active {
    color: #fff;
    background-color: #2c72ba;
  }

  }
  }
  }

  .edit{
    padding: 3px 15px;
    border: 1px solid #1a57a0;
    background-color: #143759;
    border-radius: 50px;
    display: inline-block;
    margin-left: 60px;
    cursor: pointer;
    img{
      position: relative;
      top: 1px;
    }
    span{
      font-size: 14px;
      margin-left: 5px;
    }
    }
  }
</style>



