import Vue from 'vue'
import Router from 'vue-router'

import Analyse from 'pages/person/analyse.vue';
import Areas from 'pages/area/area'
import CommitBigData from 'pages/commitStudy/commitBigData'
import CrimeHot from 'pages/commitStudy/crimeHot'
import Gridding from 'pages/commitStudy/gridding'
import Person from 'pages/person/person'
import Index from 'pages/index/index'
import Time from 'pages/time/time'
import Type from 'pages/type/type'
import Login from 'pages/login/login'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/',
    redirect: 'login'
  }, {
      path: '/login',
      name: 'login',
      component: Login,
    meta: {
        title: '登录-猎鲨大数据'
    }
    },
    {
      path: '/commitBigData',
      name: 'commitBigData',
      redirect: '/commitBigData/crimeHot',
      component: CommitBigData,
      children: [{
        path: 'crimeHot',
        name: 'crimeHot',
        component: CrimeHot,
        meta: {
          title: '犯罪热点研判-猎鲨大数据'
        }
      },
        {
          path: 'gridding',
          name: 'gridding',
          component: Gridding,
          meta: {
            title: '网络化研判-猎鲨大数据'
          }
        },
      ],
    }, {
      path: '/index',
      name: 'index',
      component: Index,
      meta: {
        title: '首页-猎鲨大数据'
      }
    }, {
      path: '/analyse',
      name: 'analyse',
      component: Analyse,
      redirect: '/analyse/timedis',
      children: [
        {
          path: 'timedis',
          name: 'timedis',
          component: Time,
          meta: {
            title: '时间分布-猎鲨大数据'
          }
        }, {
          path: 'areas',
          name: 'areas',
          component: Areas,
          meta: {
            title: '地域分布-猎鲨大数据'
          }
        }, {
          path: 'type',
          name: 'type',
          component: Type,
          meta: {
            title: '类型分布-猎鲨大数据'
          }
        }, {
          path: 'person',
          name: 'person',
          component: Person,
          meta: {
            title: '人员分布-猎鲨大数据'
          }
        },
      ]
    }
  ],
  linkActiveClass: 'active'
})
router.beforeEach((to, from, next) => {
  if (to.path === '/login' || localStorage.getItem('accessToken')) {
    next()
  } else if (!localStorage.getItem('accessToken')) {
    next({path: '/'})
  }
})
// 导航确认以后才去改变title
router.afterEach((to) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title
  }
})
export default router



